import { useState } from "react";
import { data } from "./data";

const App = () => {
  const [para, setPara] = useState(0);
  const [words, setWords] = useState(0);
  const [text, setText] = useState([]);

  // handle form submission and number of paragraphs wanted
  const showText = (e) => {
    e.preventDefault();

    let paraNumber = parseInt(para);

    if (paraNumber <= 0) {
      window.alert(
        "Please enter an appropraite number to generate the number of paragraphs you want"
      );
      return;
    }

    setText(data.slice(0, paraNumber));
  };

  // handle number of words wanted
  const getWords = (paragraph, no_words) => {
    return paragraph.split(" ").splice(0, no_words).join(" ");
  };

  return (
    <>
      <main className="wrapper">
        <h1 className="content-heading">Random Text Generator</h1>
        <section className="form-wrapper">
          <h3 className="sub-heading">Enter your specifications below...</h3>
          <form className="form" onSubmit={showText}>
            <div className="form-group">
              <label htmlFor="paragraphs">Number of paragraphs:</label>
              <input
                type="number"
                name="paragraphs"
                min="0"
                max="6"
                value={para}
                onChange={(e) => setPara(e.target.value)}
              />
            </div>
            <div className="form-group">
              <label htmlFor="words">Words per paragraph:</label>
              <input
                type="number"
                name="words"
                min="0"
                max="210"
                value={words}
                onChange={(e) => setWords(e.target.value)}
              />
            </div>
            <button type="submit">Generate</button>
          </form>
        </section>
        <section className="paragraphs">
          {text.map((textItem) => {
            const { id, content } = textItem;
            return (
              <p className="each-paragraph" key={id}>
                {getWords(content, parseInt(words))}
              </p>
            );
          })}
        </section>
      </main>
    </>
  );
};

export default App;
